.global-div-header{
  margin: auto;
}

.Barre-navigation{
  width: 100%;
  height: 65px;
  top: 0px;
  display: flex;
  justify-content: space-between;
  background-color: white;
  color: black;
  position: fixed;
  z-index: 2;
  box-shadow: rgba(0, 0, 0, 0.185) 0px 10px 10px -10px;
}

.Barre-text {
  width: 80%;
  display: flex;
  justify-content: space-around;
}

.logo{
  object-fit: cover;
  padding: 10px;
  max-width: 500px;
}

.navBarDiv{
  display: flex;
  width: 80%;
  justify-content: end;
}

.headerNav-li, .servicesNav{
  color: #002556;
  list-style: none;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  font-weight: bold;
}

.listeCantacUs{
  list-style: none;
}

.contactLi{
  font-weight: bold;
}



.headerNav-li:hover{
  cursor:pointer;
}
.menu-link {
  display: flex;
  outline: none;
  text-decoration: none;
}

.menu-link:visited {
  color: white;
}

/*MENU BURGER*/

.App-header {
  display: none;
  padding: 20px;
  padding-left: 5px;
  padding-right: 5px;
}

.burger-button {
  background: none;
  border: none;
  color: #002556;
  font-size: 24px;
  cursor: pointer;
}


.menu {
  display: none;
  position: absolute;
  top: 60px;
  right: 0px;
  background-color: none;
  color: white;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  background-color: white;
}

.menu.open {
  display: block;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  margin-bottom: 10px;
}

a {
  color: white;
  text-decoration: none;
}
/*fin menu burger*/

.sous{
  display: none;
    background-color: rgb(0 37 86);
    position: absolute;
    max-width: 200px;
    z-index: 1000;
    text-align: center;
    list-style: none;
    text-decoration: none;
    padding: 20px;
    padding-left: 10px;
    padding-top: 10px;
    color: white;

}
nav > ul li:hover .sous{
  display: block;
}
.sous li{
  float: none;
  width: 100%;
  text-align: left;
  margin-top: 10px;
  color: white;
}
.sous a{
  padding: 10px;
  border-bottom: none;
}
.sous a:hover{
  border-bottom: none;
  background-color: RGBa(200,200,200,0.1);
}
.deroulant > a::after{
  content:" â–¼";
  font-size: 12px;
}

.conteneur{
height: 1500px;
}

button {
  background-color: #002556;
  color: white;
  padding: 1.5px;
  padding-left: 5px;
  padding-right: 5px;
  border: none;
  cursor: pointer;
}

/*Responsive*/
@media screen and (max-width: 1240px) {
	.App-header{
    margin: 0px;
  }
}

@media screen and (max-width: 830px) {

	.App-header{
    margin: 0px;
    display: flex;
  }
  
  .sous {
    position: relative;
  }

  .Barre-text li{
    width: 100%;
    text-align: center;
    font-size: 10px;
  }

  .Barre-text{
    display: none;
    justify-content: center;
  }

}